.navbar:before {
	content: ' ';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	// background: $white !important
	background: $head-foot;
	opacity: 0.5; }
.navbar {
	border: none;
	margin: 0;
	height: 50px;
	opacity: 1.5;
	li {
		position: relative;
		a {
			@include transition-ease(0.5s);
			color: $white; } }

	li:hover a {
		color: $head-foot !important;
		background: $white !important; }

	.navbar-brand {
		color: $white;
		position: relative;
		font-size: 26px;
		span:first-child {
			font-weight: bold;
			color: $crimson;
			font-size: 32px; } }

	.navbar-toggle {
		background: $head-foot;
		opacity: 0.8;
		span {
			background: $white; } }
	.navbar-nav {
		margin-top: 0;
		margin-bottom: 0; } }



.main-head-slider {
	height: 100%;

	.sl-slider-h {
		position: relative;
		text-align: center;
		top: 45%;
		left: 50%;
		font-weight: bold;
		font-size: 70px;
		color: $head-foot;
		display: inline-block;
		@include translate(-50%, -50%);
		span {}
		span:last-child {
			margin-top: 20px;
			font-size: 25px;
			display: block; }
 }		// left: 50%
	.container {
		padding: 0; }
	.cover:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $black !important;
		opacity: 0.3; }
	.phone {
		font-weight: bold;
		letter-spacing: 2px; }

	.sl-slide:nth-child(2) {
		h2 {
			margin-top: 40px; } }

	.carousel-inner {
 }		// height: 100vh

	.item {
		background: url('/img/slider-bg.png') no-repeat 0 50%;
		background-size: cover;
		// min-height: 550px
		height: 100%;
		text-align: center;
		// padding: 0 5% 20px
		color: $white;
		.content {
			position: absolute;
			top: 50%;
			left: 50%;
			@include translate(-50%, -50%); }
		h2 {
			text-shadow: 0 0 1px #000;
			padding: 20px;
			font-size: 34px;
			font-weight: 700;
			letter-spacing: 13px;
			text-transform: uppercase;
			width: 80%;
			text-align: center;
			line-height: 50px; } } }


