.contact:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: $price;
	opacity: 0.8; }
.contact {
	position: relative;
	.contact-content {
		position: relative; }
	.contact-numbers {
		margin-bottom: 30px;
		.about-h {
			font-size: 44px; }
		div {
			font-size: 20px; }
		.contact-caption {
			font-weight: bold;
			margin: 10px 0; }
